import uniIcon from "@/components/uni-icon/uni-icon.vue"
import QsTabs from "@/components/QS-tabs/QS-tabs.vue"
import CommApi from "@/pages/comm/api.js"

export default {
	data() {
		return {
			studentList: [],
			startTime: null,
			currentNum: 0,
			selected: [],
			dateLength: 14, // 显示的天数
			teacherid: null,
		}
	},
	created() {
		this.userinfo = uni.getStorageSync("userinfo")
		this.teacherid = this.userinfo && this.userinfo.id;
		this.getTime();
		this.getSubStudents();
	},
	methods: {
		getSubStudents() {
			uni.request({
				url: CommApi.api.getActiveSubTeacher.real,
				data: {
					yydate: this.selected[this.currentNum].date,
					teacherid: this.teacherid || 2,
				},
				method: 'POST',
				header: {
					"Access-Control-Allow-Origin": '*',
					"content-type": "application/json",
				},
				success: (res) => {
					if (res.statusCode === 200 && res.data.code === 1) {
						this.studentList = res.data.data;
					} else {
						uni.showToast({
							title: res.data && res.data.msg || res.data.error,
							icon: "none"
						});
					}
				}
			});
		},
		colorHash(index) {
		    return `avatar-color-${this.simpleHash(index)}`;
		},
		simpleHash(key, mod = 8) {
			return Math.abs(key % mod);
		},
		getTime() {
			const day = new Date();
			const lastDay = this.getLastDay(day.getFullYear(), day.getMonth() + 1);
			const currentYear = day.getFullYear();
			const currentMonth = day.getMonth() + 1;
			const currentDay = day.getDate();
			const count = lastDay - currentDay;
			this.startTime = `${currentYear}-${currentMonth}-${currentDay}`;
			if (count >= 6) {
				for (let index = 0; index < this.dateLength; index++) {
					let day = currentDay + index;
					if (day < 10) day = '0' + day;
					const week = this.getWeek(`${currentYear}-${currentMonth}-${day}`);
					this.selected.push({
						date: `${currentYear}-${currentMonth}-${day}`,
						name: currentDay + index,
						week,
					});
				}
			} else {
				const num = 6 - count;
				switch (currentMonth) {
					case 12:
					for (let index = 0; index < this.dateLength; index++) {
						if (index <= count) {
							let day = currentDay + index;
							if (day < 10) day = '0' + day;
							const week = this.getWeek(`${currentYear}-${currentMonth}-${day}`);
							this.selected.push({
								date: `${currentYear}-${currentMonth}-${day}`,
								name: currentDay + index,
								week,
							});
						} else {
							let day = index - count;
							if (day < 10) day = '0' + day;
							const week = this.getWeek(`${currentYear + 1}-01-${day}`);
							this.selected.push({
								date: `${currentYear + 1}-01-${day}`,
								name: index - count,
								week,
							});
						}
					}
						break;
					default:
						const count1 = 0;
						for (let index = 0; index < this.dateLength; index++) {
							if (index <= count) {
								let day = currentDay + index;
								if (day < 10) day = '0' + day;
								const week = this.getWeek(`${currentYear}-${currentMonth}-${day}`);
								this.selected.push({
									date: `${currentYear}-${currentMonth}-${day}`,
									name: currentDay + index,
									week,
								});
							} else {
								let day = index - count;
								if (day < 10) day = '0' + day;
								const week = this.getWeek(`${currentYear}-${currentMonth + 1}-${day}`);
								this.selected.push({
									date: `${currentYear}-${currentMonth + 1}-${day}`,
									name: index - count,
									week,
								});
							}
						}
						break;
				}
			}
		},
		getLastDay(year, month) {
			var d = new Date(0);
			if (month == 12) {
				d.setUTCFullYear(year + 1);
				d.setUTCMonth(0);
			} else {
				d.setUTCFullYear(year);
				d.setUTCMonth(month);
			}
			d.setTime(d.getTime() - 1);
			return d.getUTCDate();
		},
		getWeek(date) {
			var d = new Date(date);
			var days = d.getDay();
			switch (days) {
				case 1:
					return '一';
					break;
				case 2:
					return '二';
					break;
				case 3:
					return '三';
					break;
				case 4:
					return '四';
					break;
				case 5:
					return '五';
					break;
				case 6:
					return '六';
					break;
				case 0:
					return '日';
					break;
			}
		},
		changeTab(val) {
			this.currentNum = val;
			this.getSubStudents();
		},
	},
	components: {
		uniIcon,
		QsTabs
	}
}
